import Activity from "../common/Activity";
import {Button, Form, Input, Tabs} from "antd";
import React from "react";
import HttpClient from "../utils/HttpClient";

const FormItem = Form.Item;
var md5 = require('md5');

class ResetPsdActivity extends Activity {

    constructor(props) {
        super(props);
    }

    handleSubmit = e => {
        this.showLoading();
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                let url = '/api/student/psd/';
                let account = {old:md5(values.old), new:md5(values.password)};
                HttpClient.post(url, account).then(data => {
                    this.hideLoading();
                    alert('');
                    localStorage.hasLogin=false;
                    this.props.history.push('/login/');
                }).catch(error => {
                    this.hideLoading();
                    if (error.code > 0) {
                        if(error.code==1){
                            alert('')
                        }
                        console.log(error);
                    } else {
                        HttpClient.handleCommonError(this.props.history, error)
                    }
                });
            } else {
                this.hideLoading();
            }
        });
    };

    compareToFirstPassword = (rule, value, callback) => {
        const form = this.props.form;
        if (value && value !== form.getFieldValue('password')) {
            callback('!');
        } else {
            callback();
        }
    };
    render() {
        const {getFieldDecorator} = this.props.form;
        return this.createLoading(
            <Form onSubmit={this.handleSubmit}>
                <FormItem label="">
                    {getFieldDecorator('old', {
                        rules: [{
                            required: true, message: '!',
                        },],
                    })(
                        <Input type="password" placeholder=""/>
                    )}
                </FormItem>
                <FormItem label="">
                    {getFieldDecorator('password', {
                        rules: [{
                            required: true, message: '!',min:6
                        },],
                    })(
                        <Input type="password" placeholder=""/>
                    )}
                </FormItem>
                <FormItem label="">
                    {getFieldDecorator('confirm', {
                        rules: [{
                            required: true, message: '!',
                        }, {
                            validator: this.compareToFirstPassword,
                        }],
                    })(
                        <Input type="password" placeholder=""/>
                    )}
                </FormItem>
                <FormItem>
                    <Button type="primary" htmlType="submit">  </Button>
                </FormItem>
            </Form>
        )
    }
}

export default Form.create()(ResetPsdActivity);


